.advantage {
  padding-top: 170px;
  margin-bottom: 1700px;

  .cardWrapper {
    margin: 0 auto;
    display: flex;

    > * {
      flex-basis: 50%;
    }
  }

  .card1 {
    padding-top: 9px;
    padding-left: 17px;
    margin-bottom: 307px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: -303px;
      left: 0px;
      width: 700px;
      height: 950px;
      background-image: url("../../assets/img//card_1_bg_1.png");
      background-repeat: no-repeat;
      z-index: -1;
    }

    &::after {
      content: "";
      position: absolute;
      top: 312px;
      left: 29px;
      width: 250px;
      height: 250px;
      background-image: url("../../assets/img/card_1_bg_2.png");
      background-repeat: no-repeat;
      z-index: -1;
    }

    .imgWrapper {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: -31px;
        left: 44px;
        width: 570px;
        height: 435px;
        background-image: url("../../assets/img/card_1_chat.png");
        background-repeat: no-repeat;
      }

      &::after {
        content: "";
        position: absolute;
        top: 14px;
        right: 51px;
        width: 250px;
        height: 430px;
        background-image: url("../../assets/img/card_1_mobile.png");
        background-repeat: no-repeat;
      }
    }

    h1 {
      color: #000;
      font-size: 36px;
    }

    h2 {
      margin-bottom: 34px;
      max-width: 500px;
    }

    p {
      margin-bottom: 43px;
      max-width: 500px;
      color: #333333;
    }
  }

  .card2 {
    position: relative;

    .content {
      padding-left: 172px;

      h2 {
        margin-bottom: 34px;
        max-width: 300px;
      }

      p {
        margin-bottom: 43px;
        max-width: 500px;
      }
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -48px;
      right: 0px;
      width: 580px;
      height: 800px;
      background-image: url("../../assets/img/card_2_bg_1.png");
      background-repeat: no-repeat;
      z-index: -1;
    }

    &::before {
      content: "";
      position: absolute;
      top: -111px;
      right: 117px;
      width: 184px;
      height: 184px;
      background-image: url("../../assets/img/card_2_bg_2.png");
      background-repeat: no-repeat;
    }

    .imgWrapper {
      position: relative;

      img {
        margin-left: -11px;
        margin-top: -54px;
      }

      &::before {
        content: "";
        position: absolute;
        top: 56px;
        left: 100px;
        width: 500px;
        height: 500px;
        background-image: url("../../assets/img/card_2_circleBlock.png");
        background-repeat: no-repeat;
      }

      // &::before {
      //   content: "";
      //   position: absolute;
      //   top: 0;
      //   right: 0;
      //   width: 400px;
      //   height: 400px;
      //   background-image: url("../../assets/img/card_2_message.png");
      //   background-repeat: no-repeat;
      // }
    }
  }
}
